export default {
	data() {
		return {
			orderid:"",
			paylist:{}
		}
	},
	onLoad:function(ex){
		console.log(ex);
		this.orderid=ex.orderid;
	},
	onShow:function(e){
		uni.getStorage({
			key:"paymentOrder",
			success: (res) => {
				for(var i=0;i<res.data.length;i++){
					if(this.orderid==res.data[i].orderid){
						this.paylist=res.data[i]
					}
				}
			}
		});
	},
	methods: {
		lookorder:function (e){
			uni.reLaunch({
				url:"../../shopcart/myorder/forpayment/forpayment"
			})
		},
		returnhome:function (e){
			uni.reLaunch({
				url:"../../home/home/home"
			})
		},
	}
}
